#root,html,body{
  height: 100vh;
}

body{
  margin: 0;
  background-image:linear-gradient(#2E3192, #1BFFFF);
  
  
}


*{
  box-sizing: border-box;
}

html{
  scroll-behavior: smooth;
}
.logoContainer{
  border: 2px solid #42b0f8;
    width: 15.5vh;
    height: 16.5vh;
    border-radius: 8vh;
    margin-top: 5vh;
    margin-left: 2vh;
    position: relative;
}
.logo-img{
  height:12vh;
    margin-left: 4vh;
    border: 1px solid #42b0f8;
    border-radius: 7vh;
    margin-top: 2vh;
  position: relative;
}
.card-container{
  display: flex;
}

@media only screen and (max-width: 600px) {
  .logoContainer{
    display: none;
    height: 30vh;
  }
  body{
    background-image:linear-gradient(#2E3192, #1BFFFF)
  }
}